.errors {
  position: absolute;
  align-items: flex-start;
  font-size: 14px;
  width: 320px;
  color: #666;
}
.inputBox {
  width: 330px;
  height: 50px;
  background-color: #222;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
}
.indicatorBox {
  width: 330px;
  height: 110px;
  display: flex;
  justify-content: space-between;
  padding: 15px;
}
.inputBox input {
  width: 100%;
  height: 100%;
  background-color: transparent;
  border: hidden;
  outline: none;
  font-size: 18px;
  text-indent: 2px;
  padding: 5px;
  color: white;
}
.inputBox span {
  cursor: pointer;
  color: #666;
}
h4 {
  color: #666;
  margin-bottom: 30px;
}
